/**
 * #.# Editor Styles
 *
 * CSS for just Backend enqueued after style.scss
 * which makes it higher in priority.
 */

 .wp-block-editorial-introparagraph {
 	textarea {
		font-size: 1.18169em;
		line-height: 1.25887;
		color: #3f2f39;
		font-family: "Tiempos-Headline";
		font-weight: 700;
	}

	.wp-block-editorial-introparagraph-content:focus,
	.wp-block-editorial-introparagraph-content:focus-within {
		svg {
			display: none !important;
		}
	}

}
